
























































































































import Vue from "vue";

const recipeBase = {
  difficulty: "",
  title: "",
  description: "",
  image: "",
  subtitle: "",
  cook_time: 1,
  tags: [],
  utensils: [],
  allergens: [],
};

export default Vue.extend({
  name: "CreateRecipe",
  props: {
    dialog: Boolean,
    itemToUpdate: Object,
  },
  watch: {
    itemToUpdate(val) {
      if (val !== undefined) {
        this.recipe = {
          difficulty: val.difficulty,
          title: val.title,
          description: val.description,
          image: val.image,
          subtitle: val.subtitle,
          cook_time: val.cook_time,
          tags: val.tags,
          utensils: val.utensils,
          allergens: val.allergens,
        };
      }
    },
  },
  methods: {
    async edit() {
      try {
        this.disabledDialog = true;
        await (this as any).$http.put(`/recipes/${this.itemToUpdate.id}`, {
          ...this.recipe,
        });
        this.$emit("item-updated");
        this.recipe = { ...recipeBase };
        this.close();
      } catch (error) {
        this.apiError = error;
        this.snackbar = true;
      } finally {
        this.disabledDialog = false;
      }
    },
    async save() {
      try {
        this.disabledDialog = true;
        await (this as any).$http.post(`/recipes`, { ...this.recipe,cook_time : parseInt(this.recipe.cook_time+"") });
        this.$emit("item-created");
        this.recipe = { ...recipeBase };
        this.close();
      } catch (error) {
        this.apiError = error;
        this.snackbar = true;
      } finally {
        this.disabledDialog = false;
      }
    },
    isEmpty: (val: string) => val.length === 0,
    close() {
      this.$emit("close-dialog");
    },
  },
  computed: {
    isEditMode() {
      return this.itemToUpdate !== undefined;
    },
  },
  data: () => ({
    rules: {
      greatherThanZero: (value: number) => value > 0 || "Greather than Zero.",
    },
    apiError: "",
    snackbar: false,
    disabledDialog: false,
    recipe: {
      ...recipeBase,
    },
  }),
});
